import React from 'react';
import './App.sass';
import { Route, BrowserRouter, Switch } from 'react-router-dom';

import Nav from '../Nav/Nav';
import Main from '../Main/Main';
import About from '../About/About';
import Portfolio from '../Portfolio/Portfolio';
import Blog from '../Blog/Blog';
import Contacts from '../Contacts/Contacts';
import NoMatchPage from '../NoMatchPage/NoMatchPage'
import ViewWork from "../Portfolio/ViewWork/ViewWork";


function App() {
	return(
	  <BrowserRouter>
		 <div className='wrapper'>
			<div className="video-bg">
			  <video width="100%" height="auto" preload="auto" autoPlay="autoplay" loop="loop">
				 <source src="videoplayback.mp4" type="video/mp4"></source>
			  </video>
			</div>
			<nav className='nav'>
			  <Nav />
			</nav>
			<main className='main'>
			  <Switch>
				 <Route exact path='/' component={ Main } />
				 <Route exact path='/about' component={ About } />
				 <Route exact path='/portfolio' component={ Portfolio } />
				 <Route exact path='/blog' component={ Blog } />
				 <Route exact path='/contacts' component={ Contacts } />
				 <Route exact path="/portfolio/:id"  component={ ViewWork }/>
				 <Route   component={NoMatchPage} />
			  </Switch>
			</main>
		 </div>
	  </BrowserRouter>
	);
}

export default App;
